%full-size {
  display: block;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

body {
  margin: 0;
}

#game-wrapper {
  @include meyer-reset;
  position: relative;
}

#bg {
  @extend %full-size;
  position: absolute;
  top: 0;
  left: 0;
  img {
    @extend %full-size;
    filter: blur(20px) brightness(80%);
  }
}

#game-wrapper,
#game,
#board {
  @extend %full-size;
}

#game {
  position: absolute;
  z-index: 10;
  background-color: $color-board-outer;
  border-radius: $board-border-radius-outer;
  box-shadow: 0 0 0.3em rgba(0, 0, 0, 1), 0 0 0.1em rgba(0, 0, 0, 1);
  transition: box-shadow 0.2s 1s;
  &.animationInProgress {
    transition: none;
    box-shadow: none;
  }
}

#board {

  position: relative;
  box-shadow: 0 0 0 $tiles-spacing $color-board-inner;
  border-radius: $board-border-radius-inner;
  background: $color-board-inner;


  .tile {
    position: absolute;
    cursor: pointer;
    transform: translateZ(0);
    transform-style: preserve-3d;
    backface-visibility: hidden;
    &:focus {
      outline: 0;
    }
    &.hole {
      display: none;
    }

    .noTouch &:hover,
    &.pressed {
      .inner {
        transform: scale(0.97);
        transition-duration: 10ms;
        transition-timing-function: ease-in-out;
        will-change: transform, transition;
      }
      .number {
        // color: $color-numbers-default-pressed;
      }
    }
    &.even-row.odd-col,
    &.odd-row.even-col {
      .inner {
        // background-color: $color-tiles-alternate;
      }
      .number {
        // color: $color-numbers-alternate;
      }
      .noTouch &:hover,
      &.pressed {
        .inner {
          // background-color: $color-tiles-alternate-pressed;
        }
        .number {
          // color: $color-numbers-alternate-pressed;
        }
      }
    }
    
  } // tile

  .inner {
    @extend %full-size;
    margin: 0;
    box-sizing: border-box;
    box-shadow: 0 0 0 $tiles-spacing $color-board-inner inset;
    background-color: $color-tiles-default;
    background-size: auto;
    background-repeat: no-repeat;
    border-radius: $board-border-radius-inner + $tiles-spacing;
    // transition-property: color, background-color, transform;
    // transition-duration: 200ms;
    // transition-timing-function: ease-out;
    
    &.selected {
      transition: none;
    }
  }

  .number {
    @extend %full-size;
    line-height: 200%;
    text-align: center;
    font-family: sans-serif;
    color: $color-numbers-default;
    text-shadow: 0.01em 0.01em 0.1em rgba(0, 0, 0, 1);
  }
 
 
} // board

